import React from 'react';
import {Button, Container, Navbar as BootstrapNavbar} from "react-bootstrap";
import GoogleLogin, {
    GoogleLoginResponse,
    GoogleLoginResponseOffline,
    GoogleLogout
} from "react-google-login";
import jwtDecode from "jwt-decode";
import "./Navbar.css";

interface UserProfile {
    token: string,
    pfpURL: string
}

export default class Navbar extends React.Component<any, { profile: UserProfile | null }> {
    constructor(props: any) {
        super(props);
        this.state = { profile: null };
    }

    render() {
        return (
            <BootstrapNavbar sticky="top" className="Navbar">
                <Container>
                    <BootstrapNavbar.Brand href="/">Digital Literacy</BootstrapNavbar.Brand>
                    <BootstrapNavbar.Toggle aria-controls="basic-navbar-nav" />
                    <LoginButton
                        signedIn={ this.state.profile !== null }
                        pfpURL={ this.state.profile?.pfpURL ?? null }
                        onSuccess={ response => {
                            console.log(response);
                            if ("tokenId" in response && "profileObj" in response) {
                                this.setState({
                                    profile: {
                                        token: response.tokenId,
                                        pfpURL: response.profileObj.imageUrl
                                    }
                                })
                            }
                        }}
                        onLoginFailure={ response => {
                            console.log(response);
                        }}
                        onLogoutSuccess= { () => {
                            this.setState({
                                profile: null
                            })
                        }}
                    />
                </Container>
            </BootstrapNavbar>
        );
    }

    componentDidMount() {
        const loginToken = localStorage.getItem("loginToken");
        if (loginToken !== null) {
            new Promise<{ token: string, pfpURL: string }>((resolve, reject) => {
                const decodedJWT: any = jwtDecode(loginToken)
                if ("picture" in decodedJWT) {
                    console.log(decodedJWT);
                    resolve({
                        token: loginToken,
                        pfpURL: decodedJWT.picture
                    })
                } else {
                    reject()
                }
            })
                .then(profile => {
                    this.setState({
                        profile: profile
                    })
                })
        }
    }

    componentDidUpdate(prevProps: Readonly<any>, prevState: Readonly<{ profile: { token: string; pfpURL: string } | null }>, snapshot?: any) {
        if (prevState.profile?.token !== this.state.profile?.token) {
            if (this.state.profile === null) {
                localStorage.removeItem("loginToken")
            } else {
                localStorage.setItem("loginToken", this.state.profile.token)
            }
        }
    }
}

function LoginButton(props: {
    signedIn: boolean,
    pfpURL: string | null
    readonly onSuccess?: (response: GoogleLoginResponse | GoogleLoginResponseOffline) => void,
    readonly onLoginFailure?: (error: any) => void,
    readonly onLogoutSuccess?: () => void;
    readonly onLogoutFailure?: () => void;
}): JSX.Element {
    if (props.signedIn) {
        console.log(props.pfpURL);
        return <div>
            {
                props.pfpURL !== null ?
                    (<img src={props.pfpURL} alt="Profile" className="pfp"/>)
                    : null
            }
            <GoogleLogout
                clientId="1037281959356-1lomvo3e69p8j305qge2hfjrshk90vvh.apps.googleusercontent.com"
                onLogoutSuccess={props.onLogoutSuccess}
                onFailure={props.onLogoutFailure}
                render={props =>
                    <Button variant="outline-primary" onClick={props.onClick} disabled={props.disabled}>Log Out</Button>
                }
            />
        </div>
    } else {
        return (
            <GoogleLogin
                clientId="1037281959356-1lomvo3e69p8j305qge2hfjrshk90vvh.apps.googleusercontent.com"
                onSuccess={props.onSuccess}
                onFailure={props.onLogoutFailure}
                render={props =>
                    <Button variant="outline-primary" onClick={props.onClick} disabled={props.disabled}>Sign In</Button>
                }
                isSignedIn={true}
            />
        );
    }
}