interface LessonProps {
    highestStep: number
}

class LessonSubelement<P, S> extends React.Component<P, Omit<S, "currentStep"> & { currentStep: number }> {
    constructor(props: P) {
        super(props);

        this.setState({
            currentStep: 0
        } as Omit<S, "currentStep"> & { currentStep: number }) //TODO: Better way to do this than to cast?
    }

    updateStep(newStep: number) {
        this.setState({
            currentStep: newStep
        } as Omit<S, "currentStep"> & { currentStep: number })
    }
}

