import React, {ForwardedRef} from "react";
import {Col, Container, Row} from "react-bootstrap";
import Navbar from "../Navbar";
import Lesson from "./Lesson";
import highlight from "../assets/misinformation/highlight.png"
import think from "../assets/misinformation/think.png"
import { RecordCircleFill as Bullet } from "react-bootstrap-icons"
import "./Misinformation.css"
import {clamp} from "../util/clamp";

export default class Misinformation extends React.Component<any, { progress: Progress }> {
    constructor(props: any) {
        super(props);

        this.state = {
            progress: Progress.Start
        }
    }

    private analyzingArticlesRef: React.RefObject<HTMLElement> = React.createRef();
    private analyzingArticlesObserver: IntersectionObserver = new IntersectionObserver((entries: IntersectionObserverEntry[], observer: IntersectionObserver) => {
        // if (entries[0].intersectionRatio > 0.8 && this.state.progress >= Progress.ThirdPerson) {
        //     this.setState({ progress: Progress.ArticlesStep3 })
        // }
    }, {
        threshold: 0.9
    })
    private discussionRef: React.RefObject<Discussion> = React.createRef();
    private conclusionRef: React.RefObject<Conclusion> = React.createRef();

    componentDidUpdate(prevProps: Readonly<any>, prevState: Readonly<{ progress: Progress }>, snapshot?: any) {
        console.log(this.state.progress)
        console.log(Progress.ArticlesStep3)
        console.log(this.state.progress <= Progress.ArticlesStep3)

        if (/*prevState.progress == Progress.Start && */this.state.progress >= Progress.ArticlesStep1 && this.state.progress <= Progress.ArticlesStep3) {
            console.log(`Scroll!!! ${this.analyzingArticlesRef.current}`)
            this.analyzingArticlesRef.current?.scrollIntoView({
                behavior: "smooth"
            })
        }

        if (prevState.progress != Progress.Discussion && this.state.progress == Progress.Discussion) {
            console.log(`Scroll2!!! ${this.discussionRef.current}`)
            console.log(this.discussionRef.current)
            this.discussionRef.current?.scrollIntoView()
        }

        if (prevState.progress != Progress.Conclusion && this.state.progress == Progress.Conclusion) {
            this.conclusionRef.current?.scrollIntoView()
        }
    }

    componentDidMount() {
        let ref = this.analyzingArticlesRef.current
        if (ref !== null) {
            this.analyzingArticlesObserver.observe(ref)
        }
    }

    render() {
        return (
            <Lesson highestStep={Progress.Discussion}>
                <Navbar/>
                <Lesson.Content  onClick={() => {
                    console.log(`next progress ${nextProgress(this.state.progress)}`)
                    this.setState({
                        progress: nextProgress(this.state.progress)
                    })
                }}>
                    <Lesson.Section backgroundColor="var(--grandmas-hair)" minHeight="revert">
                        <Container>
                            <h1>Susceptibility to <br/>Misinformation</h1>
                            {/*<p>Mis asdf is bad</p>*/}
                        </Container>
                    </Lesson.Section>
                    <AnalyzingArticles ref={this.analyzingArticlesRef} progress={this.state.progress} description={this.currentDescription()}/>
                    <Discussion ref={this.discussionRef}/>
                    <Conclusion ref={this.conclusionRef}/>
                </Lesson.Content>
                <Lesson.NavigationArrows last={() => {
                    this.setState({
                        progress: previousProgress(this.state.progress)
                    })
                }} next={() => {
                    this.setState({
                        progress: nextProgress(this.state.progress)
                    })
                }}/>
            </Lesson>
        );
    }

    currentDescription(): string {
        switch (this.state.progress) {
            case Progress.Start:
                return "In this activity, you will explore misinformation, how it spreads, and how to stop it, as well as how susceptible you yourself are to misleading information.";
            case Progress.ArticlesStep1:
                return "In a moment, you'll look at a series of articles and try to determine whether or not they contain misinformation. Enter the percentage you think you'll be able to accurately label."
            case Progress.ArticlesStep2:
                return "When they show up, look at the articles on your screen and label them as \"misinformation\" or \"accurate\"."
            default:
                return "Check your final score. How well did you do?"
        }
    }
}

enum Progress {
    Start,
    ArticlesStep1,
    ArticlesStep2,
    ArticlesStep3,
    Discussion,
    Conclusion
}

const highestProgress = Progress.Conclusion

function nextProgress(currentProgress: Progress): Progress {
    return clamp(currentProgress + 1, 0, highestProgress)
    // switch (currentProgress) {
    //     case Progress.Start:
    //         return Progress.ArticlesStep1
    //     case Progress.ArticlesStep1:
    //         return Progress.ArticlesStep2
    //     case Progress.ArticlesStep2:
    //         return Progress.ArticlesStep3
    //     case Progress.ArticlesStep3:
    //         return Progress.ThirdPerson
    //     case Progress.ThirdPerson:
    //         return Progress.ThirdPerson
    // }

}

function previousProgress(currentProgress: Progress): Progress {
    return clamp(currentProgress - 1, 0, highestProgress)
    // switch (currentProgress) {
    //     case Progress.Start:
    //         return Progress.Start
    //     case Progress.ArticlesStep1:
    //         return Progress.Start
    //     case Progress.ArticlesStep2:
    //         return Progress.ArticlesStep1
    //     case Progress.ArticlesStep3:
    //         return Progress.ArticlesStep2
    //     case Progress.ThirdPerson:
    //         return Progress.ArticlesStep3
    // }
}

interface AnalyzingArticlesProps {
    progress: Progress,
    description: string
}

const AnalyzingArticles = React.forwardRef((props: AnalyzingArticlesProps, ref: ForwardedRef<HTMLElement>) => {
    return <Lesson.Section id="analyzing-articles" backgroundColor="var(--literally-just-your-shirt)" ref={ref}>
    {/*return <Lesson.Section id="analyzing-articles" backgroundColor="var(--literally-just-your-shirt)">*/}
        <Container>
            <Row>
                <Col id="analyzing-articles-images" xs="12" md="8" className="gy-4 order-2 order-md-1">
                    <Row className="layout-grid d-flex">
                        <Col xs="6" className={ "vertical-flex" + animateIfProgressIsPast(props.progress, Progress.ArticlesStep1) }>
                            <h2 className="">Step 1: Guess how well you'll do</h2>
                            <img id="think" src={think} alt="highlight"/>
                        </Col>
                        <Col xs="6" className={ "vertical-flex" + animateIfProgressIsPast(props.progress, Progress.ArticlesStep2) }>
                            <h2>Step 2: Evaluate articles</h2>
                            <img id="highlight" src={highlight} alt="highlight"/>
                        </Col>
                    </Row>
                    <Row className="layout-grid d-flex">
                        <Col xs="12" className={ "vertical-flex gy-4" + animateIfProgressIsPast(props.progress, Progress.ArticlesStep3) }>
                            <h2>Step 3: Compare your scores</h2>
                            <div id="result-image">
                                <ResultImageLine width="80%" />
                                <ResultImageLine width="40%" />
                                <ResultImageLine width="65%" />
                            </div>
                        </Col>
                    </Row>
                </Col>
                <Col xs="12" md="4" className="vertical-flex gy-4 order-1 order-md-2">
                    <h2 id="analyzing-articles-header">Analyzing Articles</h2>
                    <h3 className="activity-header">ACTIVITY</h3>
                    <p className="activity-description">{props.description}</p>
                </Col>
            </Row>
        </Container>
    </Lesson.Section>
});

function animateIfProgressIsPast(progress: Progress, target: Progress): string {
    if (progress >= target) {
        console.log(`progress ${progress} is past ${target}`)
        return " animate";
    } else {
        return "";
    }
}

function ResultImageLine(props: { width: string }): JSX.Element {
    return (
        <div className="result-image-section">
            <Bullet style={{
                width: "1.5rem",
                color: "white"
            }}/>

            <div style={{
                width: props.width
            }}>
                <div className="result-image-line"/>
            </div>
        </div>
    );
}

class Discussion extends React.Component<any, any> {
    private rootElement: React.RefObject<HTMLElement> = React.createRef()
    public scrollIntoView() {
        this.rootElement.current?.scrollIntoView({
            behavior: "smooth"
        })
    }

    render() {
        return (
            <Lesson.Section id="discussion" backgroundColor="var(--rainy-day-ocean)" ref={this.rootElement}>
                <Container>
                    <ul>
                        <li>Which questions did you get right? Which ones did you get wrong?</li>
                        <li>What techniques did you use on the ones you got right? Where did they go wrong on the ones you got wrong?</li>
                        <li>How can we be more vigilant about preventing the spread of misinformation?</li>
                    </ul>
                </Container>
            </Lesson.Section>
        );
    }
}

class Conclusion extends React.Component<any, any> {
    private rootElement: React.RefObject<HTMLElement> = React.createRef()
    public scrollIntoView() {
        this.rootElement.current?.scrollIntoView({
            behavior: "smooth"
        })
    }

    render() {
        return (
            <Lesson.Section id="conclusion" backgroundColor="var(--grandmas-hair)" ref={this.rootElement}>
                <Container>
                    <ul>
                        <li><strong>Fact check</strong> anything you read—just a quick Google search is often enough to corroborate or disprove an article.</li>
                        <li>If you hear or read about some new information, <strong>distrust it first. <u>Especially if it confirms what you already believe</u></strong>, treat it as misleading until proven accurate by a fact check or examining the source.</li>
                        <li><strong>Read beyond the headline</strong>, if you can. They often don’t match the full story.</li>
                        <li><strong>Watch for stories with an emotional appeal.</strong> Think about how an article makes you feel, and why they might want you to feel that way. The best sources let you come to your own conclusions.</li>
                    </ul>
                </Container>
            </Lesson.Section>
        );
    }
}

